import React from 'react'
import { Helmet } from 'react-helmet'
import { Link } from 'react-router-dom'
import '../css/page.scss'

export default function Hire({ lang }) {
    return (
        <>
            <Helmet>
                <title>Franklin Rosales | Hire</title>
            </Helmet>
            <div className="page hirepage">
                <header>Let's Talk!</header>
                <section>
                    I'm always ready for anything you need. You can see my complete offer at my LinkedIn page, but here's a quick summary. <br />
                    I can make websites, frontend and backend with the latest technologies, sleek design and very good performance, or if you prefer,
                    I can do data analysis and train a machine learning model to aid you in your work, no matter what it is.
                    Not what you need? Ask me to make a program with python which will automate the most burdensome parts of your work. Just say the word!
                </section>
                <Link to="/work" className="button">
                    Read more
                    <svg id="right-arrow" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 492.004 492.004">
                        <g><path d="M382.678,226.804L163.73,7.86C158.666,2.792,151.906,0,144.698,0s-13.968,2.792-19.032,7.86l-16.124,16.12 c-10.492,10.504-10.492,27.576,0,38.064L293.398,245.9l-184.06,184.06c-5.064,5.068-7.86,11.824-7.86,19.028 c0,7.212,2.796,13.968,7.86,19.04l16.124,16.116c5.068,5.068,11.824,7.86,19.032,7.86s13.968-2.792,19.032-7.86L382.678,265 c5.076-5.084,7.864-11.872,7.848-19.088C390.542,238.668,387.754,231.884,382.678,226.804z" /></g>
                    </svg>
                </Link>
            </div>
        </>
    )
}
